@import "../../global.scss";

.portfolio{
    
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f1f2f6;

    h2{
        font-size: 50px;
        color: #333;
        font-weight: 600;
        @include mobile{
            font-size: 40px;
        }
    }
    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        @include mobile{
            width: 100%;
        }
        .item{
            width: 220px;
            height: 150px;
            border-radius: 10px;
            border: 1px solid rgb(247, 244, 244);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;
            @include mobile{
                width: 130px;
                height: 120px;
            }
            .content{
                position: absolute;
                display: flex;
                flex-direction: column;
                padding: 5px;
                h3{
                    font-size: 20px;
                }
                button{
                    border: none;
                    cursor: pointer;
                    border: 1px solid;
                    background-color: rgb(210, 54, 85);
                    color: #fff;
                    margin-top: 10px;
                    width: 150px;
                    &:hover{
                        background-color: crimson;
                    }
                    a{
                        text-decoration: none;
                        color: inherit;
                       
                    }
                }
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }
            &:hover{
                background-color: $main-color;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}