@import "../../global.scss";

.menu{
    width: 300px;
    height: 100vh;
    background-color: $main-color;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition:  all 1s ease;

    &.active{
        right: 0;
    }

    ul{
        list-style: none;
        margin: 0;
        padding: 0;
        font-size: 30px;
        font-weight: 300;
        color: #fff;
        width: 60%;
    }
    li{
        margin-bottom: 25px;
        a{
            font-size: inherit;
            color: inherit;
            text-decoration: none;
        }
        &:hover{
            font-weight: 500;
        }
    }

}