@import "../../global.scss";
.contact{
    background-color: #fff;
    display: flex;
  @include mobile{
      flex-direction: column;
  }

    .left{
        flex: 1;
        overflow: hidden;

        img{
            height: 100%;
        }
    }
    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2{
            font-size: 30px;
        }

        form{
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            input{
                width: 300px;
                height: 30px;
                font-size: 14px;
                outline: none;
                margin-bottom: 10px;
                @include mobile{
                    width: 200px;
                    height: 20px;
                }
            }
            textarea{
                width: 300px;
                height: 200px;
                font-size: 14px;
                outline: none;
                @include mobile{
                    width: 200px;
                    height: 120px;
                } 
            }

            button{
                margin-top: 10px;
                width: 150px;
                height: 30px;
                color: #fff;
                background-color:rgb(221, 58, 91);
                border: none;
                border-radius: 10px;
                font-weight: 500px;
                cursor: pointer;
                outline: none;

                &:hover{
                    background-color: rgb(230, 20, 62);
                }
            }
        }
    }
}