@import "../../global.scss";

.intro{
    background-color: #fff;
    display: flex;
    @include mobile{
        flex-direction: column;
        align-items: center;
    }
    .left{
        flex: 0.5;
        overflow: hidden;
        
        .imgContainer{
            width: 400px;
            height: 400px;
            background-color: crimson;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile{
                align-items: flex-start;
                width: 700px;
                height: 700px;
            }
            

            img{
                width: 80%;
               @include mobile{
                  width: 52%;
               }
            }
        }
    }
    .right{
        flex: 0.5;
        position: relative;
        .wrapper{
            width: 100%;
            height: 100%;
           padding-left: 50px;
           display: flex;
           flex-direction: column;
           justify-content: center;

           @include mobile{
               padding-left: 0px;
               align-items: center;
           }
           h1{
            font-size: 60px;
            margin: 10px 0px;
            @include mobile{
                font-size: 40px;
            }
           }
           h2{
            font-size: 35px ;
           
           }
           h3{
            font-size: 30px;
            span{
                font-size: inherit;
                color: crimson;
            }
            .ityped-cursor{
                animation:  blink 1s infinite;
            }
            @keyframes blink {
                50%{
                    opacity: 1;
                }
                100%{
                    opacity: 0;
                }
            }

           }
        }
        a{
            position: absolute;
            bottom: 10px;
            left: 40%;

            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}